.App{
  background: url(./../public/img/background.jpg) 50% 50%/100% auto no-repeat;
  background-size: cover;
  min-height: 100vh;
  width: 100%;
  position: relative;
  font-family: 'Noto Sans KR', sans-serif;
}
h1{
  font-size: 3rem;
  position: relative;
  display: block;
  
  text-align: center;
  margin: 0;
  padding: 0;
  color: #FFF;
}
h2{
  color: #FFF;
  text-align: center;
  padding-top: 42px;
  font-size: 2rem;
}
.container{
  width: 90%;
  margin: auto;
  padding-top: 88px;
}
.inner{
  width: 100%;
  padding-top: 64px;
}
.inner>div,.inner>form{
  width: 100%;  
  color: #FFF;
  font-size: 1.2rem;
  text-align: center;
  position: relative;
  padding: 10px;
}

span.from, span.to{
  margin-left: 8px;
  display: inline-block;
}
input{
  width: 70px;
  padding: 4px;
  display: inline-block;
  vertical-align: top;
  text-align: center;
}

div.top,div.bottom{
  margin-bottom: 48px;
  font-weight: 600;
}
.btn_count{
  border: none;
  padding: 16px 24px;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  background: #FFF;
  color: #222;
  
  cursor: pointer;
}

span.blank{
  margin-right: 8px;
}
.result{
    
}
.result p{
  font-weight: 700;
  font-size: 1.5rem;
}